import Vue from "vue";
import VueRouter from "vue-router";
import auth from "./../services/auth/auth";

auth.init();
Vue.use(VueRouter);

const router = new VueRouter({
    mode: "history",
    routes: [{
            path: "/",
            name: "home",
            component: () =>
                import('./../pages/market/Home.vue')
        },
        {
            path: "/admin",
            name: "admin",
            component: () =>
                import('./../pages/admin/Admin.vue')
        },
        {
            path: "/confirm",
            name: "confirm",
            component: () =>
                import('./../pages/market/Confirm.vue'),
            meta: {
                requiresAuth: true,
                is_user: true
            }
        },
        {
            path: "/subconfirm",
            name: "subconfirm",
            component: () =>
                import('./../pages/market/SubConfirm.vue'),
            meta: {
                requiresAuth: true,
                is_user: true
            }
        },
        {
            path: "/login",
            name: "login",
            component: () =>
                import('./../pages/auth/Login.vue'),
                meta: {
                    bcLinkText: "Login"
                  }
        },
        {
            path: "/register",
            name: "register",
            component: () =>
                import('./../pages/auth/Register.vue')
        },
        {
            path: "/contact",
            name: "contact",
            component: () =>
                import('./../pages/service_page/Contact.vue'),
            meta: {
                requiresAuth: true,
                is_user: true
            }
        },
        {
            path: "/blog",
            name: "blog",
            component: () =>
                import('./../pages/blog/Post.vue')
        },
        {
            path: "/blog/single-post/:title",
            name: "blog-item",
            component: () =>
                import('./../pages/blog/SinglePost.vue'),
                props: true
        },
        {
            path: "/delivery",
            name: "delivery",
            component: () =>
                import('./../pages/service_page/Delivery.vue')
        },
        {
            path: "/garantee",
            name: "garantee",
            component: () =>
                import('./../pages/service_page/Garantee.vue')
        },
        {
            path: "/cart",
            name: "cart",
            component: () =>
                import('./../pages/market/Cart.vue'),
            meta: {
                requiresAuth: true,
                is_user: true
            }
        },
        {
            path: "/wishlist",
            name: "wishlist",
            component: () =>
                import('./../pages/market/Wishlist.vue'),
            meta: {
                requiresAuth: true,
                is_user: true
            }
        },
        {
            path: "/catalog/:slug",
            name: "catalog",
            component: () =>
                import('./../pages/market/Catalog.vue')
        },
        {
            path: "/catalog/product/:title",
            name: "product",
            component: () =>
                import('./../pages/market/Product.vue'),
            meta: {
                requiresAuth: true,
                is_user: true
            }
        },
        {
            path: "/checkout",
            name: "checkout",
            component: () =>
                import('./../pages/market/Checkout.vue'),
            meta: {
                requiresAuth: true,
                is_user: true
            }
        },
        {
            path: '/:pathMatch(.*)*',
            name: 'not-found',
            component: () =>
                import('./../pages/auth/PageNotFound.vue'),
        },


    ]
});

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!auth.loggedIn()) {
            next({
                name: "login"
            });
        } else {
            let user = JSON.parse(localStorage.getItem('user'))
            if (to.matched.some(record => record.meta.is_admin)) {
                if (user.is_admin == 1) {
                    next()
                } else {
                    next({
                        name: 'home'
                    })
                }
            } else if (to.matched.some(record => record.meta.is_user)) {
                if (user.is_admin == 0) {
                    next()
                } else {
                    next({
                        name: 'admin'
                    })
                }
            }
            next();
        }
    } else {
        next()
    }
});


export default router;
